<template>
 <v-row justify="center" align="center" >
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
<!-- v-dialog     style="height:50vh"
 -->
      <v-card style="background-color: tranparent;" class="white--text">
        <v-toolbar
          dark
          color="primary"
        >
          <v-btn
            icon
            dark
            @click="$emit('closed', dialog)"
          >
            <v-icon>mdi-close</v-icon>

                        <!-- <v-icon>mdi-check</v-icon> -->
                                                <!-- <v-icon>mdi-close</v-icon> -->

          </v-btn>
        </v-toolbar>

<div style="position:absolute">
          <Progress />
          </div>


<!-- <v-container fill-height fluid> -->

<!-- <div> #########-->
<div v-if="duringStatus == 'G'"  class="game text-center centercenter" style=" height:100vh">
 <h1 >
   {{activeWord}}
   <!-- {{wordsArray}} -->
         </h1>
</div>
<div v-if="duringStatus == 'W'" class="win text-center centercenter" style="height:100vh">
               <v-icon class="iconStyling" size="350" color="white">mdi-check</v-icon>

</div>
<div  v-if="duringStatus == 'L'" class="lose text-center centercenter" style="height:100vh">
      <v-icon class="iconStyling" size="350" color="white">mdi-close</v-icon>
</div>
  

      </v-card>

       <!-- <v-btn @click="chg('W')">WIN</v-btn>
         <v-btn @click="chg('L')">LOSE</v-btn> -->
       <!-- <v-btn @click="chg('G')">GAMMEEE</v-btn> -->

    </v-dialog>
    <!-- <Results/> -->
  </v-row>
</template>

<script>
// import Results from '@/components/Results.vue';
import Progress from '@/components/Progress.vue';
    export default {
    data () {
      return {
        dialog: true,
        showWin: false,
        duringStatus: "G", // G .. Game , W .. Win, L .. Lose
        wins:0,
        // w: this.words
        // wordsArray:["Wort1","Wort2","Wort3","Wort4"],
        activeWord: "",
        ori:'L'
      }
    },
    components: {
      Progress,
      // Results
      // Win,
    },
    props: {
      words: {
        type: Array
      },

    },
    methods: {
      // showResults(){
      //   console.log()
      //     window.location.assign("/results");
      // },
      getRandomInt(max) {
      return Math.floor(Math.random() * Math.floor(max));
      },
      newWord(){
          this.activeWord = this.wordsArray[this.getRandomInt(this.wordsArray.length)];
      },
      chg(l){
        setTimeout(() => this.clogText(), 1000);
        this.duringStatus = l;
      },
      clogText(){
        this.newWord();
        console.log("Back to game")
        this.duringStatus = 'G';
      },




      handleOrientation(event) {
               const { type } = screen.orientation;
              console.log(`Fullscreen and locked to ${type}. Ready!`);

    // await screen.orientation.lock("landscape");


            //  let ori = event.orientation;
  var absolute = event.absolute;
  var alpha    = event.alpha;
  var beta     = event.beta;
  var gamma    = event.gamma;


  console.log("**********************")
  // console.log(ori);
  console.log("ALPHA GAME: "+alpha);
  console.log("BETA GAME: "+beta);
  console.log("GAMME GAME: "+ gamma);
  console.log("**********************")

console.log("AA"+absolute+alpha+beta+gamma+"");
  // Do stuff with the new orientation data
  // this.deviceori="AA"+absolute+alpha+beta+gamma+"";
  if(gamma >= -60 &&  gamma <= -30 && alpha <= 0){
    this.duringStatus = 'W';
    console.log("WIN");

  }
  else if(gamma >= -60 &&  gamma <= -30 && alpha >= 0){
        this.duringStatus = 'L';

    console.log("alpha bigger 0")
  }
  else{
    if(this.duringStatus != 'G'){
      this.newWord();
    }
    this.duringStatus ='G';
    console.log("GAME")
  }

}
,   
    },

    created () {
      this.dialog = true;

            window.screen.orientation.lock("landscape-primary");

      window.addEventListener("deviceorientation", this.handleOrientation, true);

     this.wordsArray = this.$props.words;
      // // window.orientation(90);
      //  window.screen.orientation.lock("landscape-primary"); öööööööööööööööö
    },

     mounted() {
       this.newWord(); //
       console.log(this.$props.words);
  },

  }
</script>

<style lang="scss" scoped>
.centercenter{
height: 90vh; justify-content:center; align-items:center; display: flex
}
.game{
  background-color: #3769F5;
}

.win{
  background-color: green;
}

.lose{
  background-color: #C70D00;
}
.iconStyling{

}

</style>